<template>
  <div class="memo inner-content container">
    <div class="memo-box" v-for="memo in memoData" :key="memo.id">
      <div class="archive-year" v-for="(yearList,year) in memoData" :key="year">
        <p style="font-size: 36px;">{{year}}</p><br/>
        <time-line class="time-line-memo" v-for="(list,month) in yearList" :list="list" :key="month">
          <span slot="header" class="header">{{month}}月</span>
        </time-line>
      </div>
      <br/> <br/> <br/> <br/>
    </div>
    <page :page="page" :count="count.memo" :each-page-number="page_num" @on-page="onPage" @on-pre="onPre"
          @on-next="onNext"></page>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  import Page from "../layout/page/page";
  import TimeLine from "../layout/time-line";

  export default {
    name: "Memo",
    components: {TimeLine, Page},
    computed: {
      ...mapState({
        count: state => state.count,
        memos: state => state.memos
      }),
      memoData() {
        let data = {}
        this.memos.map(item => {
          let createdAt = item.date
          let year = new Date(createdAt).getFullYear()
          if (!data[year]) data[year] = {}
          // data[year].push(item)
          let month = new Date(createdAt).getMonth()+1
          if (!data[year][month]) data[year][month] = []
          item.title = item.content
          item.createdAt = item.date
          data[year][month].push(item)
        })
        return data
      }
    },
    data() {
      return {
        page_num: 10,
        page: 1,
      }
    },
    methods: {
      onPage(e) {
        this.getPage(e)
      },
      onPre(e) {
        if (e != this.page) {
          this.getPage(e)
        }
      },
      onNext(e) {
        console.log('next:', this.next)
        if (e != this.page) {
          this.getPage(e)
        }
      },
      getPage(page = 1) {
        let params = {page_num: this.page_num, page: page}
        this.$store.dispatch('getMemos', params).then(res => {
          if (res) {
            this.page = page
          }
        })
      }
    },
    created() {
      this.getPage()
      this.$store.dispatch('getMemoCount')
    }
  }
</script>

<style lang="scss">
  .time-line-memo.time-line {
    .time-line-items {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      border: 2px dashed #dedede;
      padding-left: 1%;
      padding-right: 1%;
      margin-bottom: 40px;
      &:after{
        content: '';
        position: absolute;
        left: -40px;
        top: 50%;
        margin-top: 30px;
        width: 39px;
        border: 1px dashed #dedede
      }
      .time-item {
        box-shadow: 1px 1px 3px 0 #ddd;
        background: #f9f9f9;
        display: inline-block;
        flex-basis: 31.33%;
        padding-right: 15px;
        padding-left: 15px;
        padding-top: 15px;
        padding-bottom: 15px;
        box-sizing: border-box;
        margin-left: 1%;
        margin-right: 1%;
        margin-bottom: 10px;
        margin-top: 10px;
        span.date{
          display: block;
          color: #0086b3;
          font-size: 16px;
          margin-bottom: 10px;
          text-align: right;
        }
        span.content{
          color: #777;
          font-size: 14px;
        }
        &:before{
          display: none;
        }
        &:after{
          display: none;
        }
      }
    }
  }
</style>



