import createDb from '../apicloud.db'
import config from '../apicloud.db.conf'

const db = createDb(config.appId, config.appKey)
import handleRequest from '../utils'

const className = 'memo'
export default {
  async getMemos(params) {
    return await handleRequest(db.getMemos(className, params))
  },
  async getMemoCount() {
    return await handleRequest(db.count(className, {}))
  },
}



